import { Endpoint } from "@reactive/client"
import { List, ListItem, Stack, StackProps } from "@reactive/ui"
import { useEffect, useState } from "react"

export interface ListEndpointsProps extends StackProps {
    children?: any
}

export function ListEndpoints({ children, ...props }: ListEndpointsProps) {
    const [endpoint] = useState(new Endpoint("dummy"))
    useEffect(() => {
        endpoint.find()
    }, [])
    return (
        <Stack {...props}>
            <List>
                <ListItem>
                    Hi
                </ListItem>
            </List>
        </Stack>
    )
}

export default ListEndpoints